#wptravel-block-trips-list {
    &.layout-one, &.layout-three, &.layout-four {
        .wptravel-blocks-trip-card {
            .wptravel-blocks-trip-meta {
                @include wptravel-blocks-trip-meta;
            }
        }
    }
}

#wptravel-block-trips-list {
    .wp-page-numbers{
        margin-top: 20px;
        a.wp-page-numbers {
            border: 1px solid #000;
        }
    }
    &.wptravel-block-wrapper {
        &.layout-one, &.layout-two, &.layout-three, &.layout-four {
            .wptravel-archive-wrapper {
                &.list-view {
                    .wptravel-blocks-trip-card {
                        .wptravel-blocks-card-body {
                            .wptravel-blocks-card-content {
                                .wptravel-blocks-trip-meta-container {
                                    .wptravel-blocks-trip-meta {
                                        align-items: baseline;
                                    }
                                }
                            }
                        }
                        @media screen and ( max-width: 768px ) {
                            flex-direction: column;

                            .wptravel-blocks-trip-card-img-container {
                                    max-width: unset;
                                
                                a {
                                    display: flex;
                                    height: 100%;

                                    img {
                                            height: 300px;
                                    }
                                }
                            }

                            .wptravel-blocks-card-body {
                                padding: 15px 10px;
                            }
                        }
                        
                        @media screen and ( max-width: 480px ) {
                            .wptravel-blocks-card-body {
                                .wptravel-blocks-card-content {
                                    flex-direction: column;
                                    gap: 0;

                                    .wptravel-blocks-trip-excerpt {
                                        width: 100%;
                                    }

                                    .wptravel-blocks-content-right {
                                        align-items: flex-start;
                                        min-height: unset;
                                    }
                                }
                            }
                        }

                        .wptravel-blocks-trip-card-top {
                            .wptravel-blocks-card-body {
                                .wptravel-blocks-card-content {
                                    .wptravel-blocks-trip-meta-container {
                                        .wptravel-blocks-trip-meta {
                                            align-items: baseline;
                                        }
                                    }
                                }
                            }
                            @media screen and ( max-width: 768px ) {
                                flex-direction: column;

                                .wptravel-blocks-trip-card-img-container {
                                        max-width: unset;
                                    
                                    a {
                                        display: flex;
                                        height: 100%;

                                        img {
                                                height: 300px;
                                        }
                                    }
                                }

                                .wptravel-blocks-card-body {
                                    padding: 15px 10px;
                                }
                            }
                            
                            @media screen and ( max-width: 480px ) {
                                .wptravel-blocks-card-body {
                                    .wptravel-blocks-card-content {
                                        flex-direction: column;
                                        gap: 0;

                                        .wptravel-blocks-trip-excerpt {
                                            width: 100%;
                                        }

                                        .wptravel-blocks-content-right, .wptravel-blocks-trip-pricing {
                                            align-items: flex-start;
                                            min-height: unset;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

